import React, { useState, useEffect } from 'react';
import { getJobStats } from '../services/api';
import {
  BarChart3, TrendingUp, Users, Briefcase, Award, Clock,
  Download, Calendar
} from 'lucide-react';
import {
  BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer,
  PieChart, Pie, Cell, LineChart, Line, Legend, AreaChart, Area
} from 'recharts';

const Reports = () => {
  const [stats, setStats] = useState(null);
  const [loading, setLoading] = useState(true);
  const [dateRange, setDateRange] = useState('all');

  useEffect(() => {
    const fetchStats = async () => {
      try {
        const res = await getJobStats();
        setStats(res.data);
      } catch (error) {
        console.error('Error fetching stats:', error);
      } finally {
        setLoading(false);
      }
    };
    fetchStats();
  }, [dateRange]);

  const COLORS = ['#10b981', '#3b82f6', '#f59e0b', '#ef4444', '#8b5cf6', '#ec4899'];

  const statusLabels = {
    PENDING: 'Ch x l',
    SCREENING: 'ang sng lc',
    INTERVIEW: 'Phng vn',
    OFFER: ' gi offer',
    REJECTED: 'T chi',
    ACCEPTED: ' nhn vic',
  };

  const exportReport = () => {
    if (!stats) return;
    
    const reportData = {
      generated_at: new Date().toISOString(),
      summary: {
        total_jobs: stats.total_jobs,
        open_jobs: stats.open_jobs,
        closed_jobs: stats.closed_jobs,
        total_applications: stats.total_applications,
        total_interviews: stats.total_interviews,
        total_offers: stats.total_offers,
        conversion_rate: stats.conversion_rate,
      },
      status_distribution: stats.status_stats,
      monthly_applications: stats.monthly_stats,
    };

    const blob = new Blob([JSON.stringify(reportData, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `recruitment-report-${new Date().toISOString().split('T')[0]}.json`;
    a.click();
    URL.revokeObjectURL(url);
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-green-600"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between flex-wrap gap-4">
        <div>
          <h1 className="text-3xl font-bold text-gray-900 mb-2">Bo co thng k</h1>
          <p className="text-gray-600">Thng k v phn tch hiu qu tuyn dng</p>
        </div>
        <div className="flex items-center gap-4">
          <select
            value={dateRange}
            onChange={(e) => setDateRange(e.target.value)}
            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500 outline-none text-gray-900 bg-white"
          >
            <option value="all">Tt c</option>
            <option value="month">Thng ny</option>
            <option value="quarter">Qu ny</option>
            <option value="year">Nm nay</option>
          </select>
          <button 
            onClick={exportReport} 
            className="px-6 py-2 bg-green-600 text-white rounded-lg font-semibold hover:bg-green-700 transition-colors flex items-center gap-2 shadow-md hover:shadow-lg"
          >
            <Download className="w-5 h-5" />
            Xut bo co
          </button>
        </div>
      </div>

      {/* Summary Cards - Light Theme */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <div className="bg-white rounded-xl shadow-md p-6 border border-gray-200 hover:shadow-lg transition-shadow">
          <div className="flex items-center justify-between mb-4">
            <div className="w-12 h-12 bg-green-100 rounded-xl flex items-center justify-center">
              <Briefcase className="w-6 h-6 text-green-600" />
            </div>
            <TrendingUp className="w-5 h-5 text-green-600" />
          </div>
          <p className="text-3xl font-bold text-gray-900 mb-1">{stats?.total_jobs || 0}</p>
          <p className="text-gray-600 text-sm font-medium">Tng vic lm</p>
          <div className="mt-2 text-sm">
            <span className="text-green-600 font-medium">{stats?.open_jobs || 0} ang tuyn</span>
            <span className="text-gray-600 mx-2"></span>
            <span className="text-gray-600">{stats?.closed_jobs || 0}  ng</span>
          </div>
        </div>

        <div className="bg-white rounded-xl shadow-md p-6 border border-gray-200 hover:shadow-lg transition-shadow">
          <div className="flex items-center justify-between mb-4">
            <div className="w-12 h-12 bg-blue-100 rounded-xl flex items-center justify-center">
              <Users className="w-6 h-6 text-blue-600" />
            </div>
          </div>
          <p className="text-3xl font-bold text-gray-900 mb-1">{stats?.total_applications || 0}</p>
          <p className="text-gray-600 text-sm font-medium">Tng h s ng tuyn</p>
          {stats?.avg_ai_score && (
            <div className="mt-2 text-sm text-blue-600 font-medium">
              im AI trung bnh: {stats.avg_ai_score.toFixed(1)}
            </div>
          )}
        </div>

        <div className="bg-white rounded-xl shadow-md p-6 border border-gray-200 hover:shadow-lg transition-shadow">
          <div className="flex items-center justify-between mb-4">
            <div className="w-12 h-12 bg-purple-100 rounded-xl flex items-center justify-center">
              <Calendar className="w-6 h-6 text-purple-600" />
            </div>
          </div>
          <p className="text-3xl font-bold text-gray-900 mb-1">{stats?.total_interviews || 0}</p>
          <p className="text-gray-600 text-sm font-medium">Tng bui phng vn</p>
          <div className="mt-2 text-sm text-green-600 font-medium">
            {stats?.completed_interviews || 0}  hon thnh
          </div>
        </div>

        <div className="bg-white rounded-xl shadow-md p-6 border border-gray-200 hover:shadow-lg transition-shadow">
          <div className="flex items-center justify-between mb-4">
            <div className="w-12 h-12 bg-green-100 rounded-xl flex items-center justify-center">
              <Award className="w-6 h-6 text-green-600" />
            </div>
          </div>
          <p className="text-3xl font-bold text-gray-900 mb-1">{stats?.conversion_rate || 0}%</p>
          <p className="text-gray-600 text-sm font-medium">T l tuyn dng thnh cng</p>
          <div className="mt-2 text-sm">
            <span className="text-green-600 font-medium">{stats?.total_offers || 0} offers</span>
            <span className="text-gray-600 mx-2"></span>
            <span className="text-red-600 font-medium">{stats?.total_rejects || 0} t chi</span>
          </div>
        </div>
      </div>

      {/* Charts Row 1 */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Status Distribution */}
        <div className="bg-white rounded-xl shadow-md border border-gray-200 p-6">
          <h3 className="text-lg font-bold text-gray-900 mb-6">Phn b trng thi h s</h3>
          <div className="h-80">
            <ResponsiveContainer width="100%" height="100%">
              <PieChart>
                <Pie
                  data={stats?.status_stats?.map(s => ({
                    name: statusLabels[s.status] || s.status,
                    value: s.count
                  })) || []}
                  cx="50%"
                  cy="50%"
                  innerRadius={60}
                  outerRadius={100}
                  paddingAngle={5}
                  dataKey="value"
                  label={({ name, percent }) => `${name} (${(percent * 100).toFixed(0)}%)`}
                  labelLine={false}
                >
                  {stats?.status_stats?.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                  ))}
                </Pie>
                <Tooltip
                  contentStyle={{ backgroundColor: '#fff', border: '1px solid #e5e7eb', borderRadius: '8px', boxShadow: '0 4px 6px -1px rgb(0 0 0 / 0.1)' }}
                  labelStyle={{ color: '#111827', fontWeight: '600' }}
                />
              </PieChart>
            </ResponsiveContainer>
          </div>
          <div className="flex flex-wrap gap-3 mt-4 justify-center">
            {stats?.status_stats?.map((s, idx) => (
              <div key={s.status} className="flex items-center gap-2">
                <div className="w-3 h-3 rounded-full" style={{ backgroundColor: COLORS[idx % COLORS.length] }}></div>
                <span className="text-sm text-gray-600 font-medium">{statusLabels[s.status] || s.status}: {s.count}</span>
              </div>
            ))}
          </div>
        </div>

        {/* Monthly Trend */}
        <div className="bg-white rounded-xl shadow-md border border-gray-200 p-6">
          <h3 className="text-lg font-bold text-gray-900 mb-6">Xu hng ng tuyn theo thng</h3>
          <div className="h-80">
            <ResponsiveContainer width="100%" height="100%">
              <AreaChart data={stats?.monthly_stats?.map(m => ({
                month: new Date(m.month).toLocaleDateString('vi-VN', { month: 'short', year: '2-digit' }),
                count: m.count
              })) || []}>
                <defs>
                  <linearGradient id="colorCount" x1="0" y1="0" x2="0" y2="1">
                    <stop offset="5%" stopColor="#10b981" stopOpacity={0.3}/>
                    <stop offset="95%" stopColor="#10b981" stopOpacity={0}/>
                  </linearGradient>
                </defs>
                <CartesianGrid strokeDasharray="3 3" stroke="#e5e7eb" />
                <XAxis dataKey="month" stroke="#6b7280" />
                <YAxis stroke="#6b7280" />
                <Tooltip
                  contentStyle={{ backgroundColor: '#fff', border: '1px solid #e5e7eb', borderRadius: '8px', boxShadow: '0 4px 6px -1px rgb(0 0 0 / 0.1)' }}
                  labelStyle={{ color: '#111827', fontWeight: '600' }}
                />
                <Area
                  type="monotone"
                  dataKey="count"
                  stroke="#10b981"
                  fillOpacity={1}
                  fill="url(#colorCount)"
                  name="S h s"
                />
              </AreaChart>
            </ResponsiveContainer>
          </div>
        </div>
      </div>

      {/* Jobs Performance */}
      <div className="bg-white rounded-xl shadow-md border border-gray-200 p-6">
        <h3 className="text-lg font-bold text-gray-900 mb-6">Hiu sut theo v tr tuyn dng</h3>
        <div className="h-80">
          <ResponsiveContainer width="100%" height="100%">
            <BarChart
              data={stats?.jobs_stats?.map(j => ({
                name: j.title?.length > 20 ? j.title.substring(0, 20) + '...' : j.title,
                applications: j.app_count,
                positions: j.positions_count,
              })) || []}
              layout="vertical"
            >
              <CartesianGrid strokeDasharray="3 3" stroke="#e5e7eb" />
              <XAxis type="number" stroke="#6b7280" />
              <YAxis dataKey="name" type="category" stroke="#6b7280" width={150} />
              <Tooltip
                contentStyle={{ backgroundColor: '#fff', border: '1px solid #e5e7eb', borderRadius: '8px', boxShadow: '0 4px 6px -1px rgb(0 0 0 / 0.1)' }}
                labelStyle={{ color: '#111827', fontWeight: '600' }}
              />
              <Legend />
              <Bar dataKey="applications" fill="#3b82f6" name="S ng vin" radius={[0, 4, 4, 0]} />
              <Bar dataKey="positions" fill="#10b981" name="S v tr cn tuyn" radius={[0, 4, 4, 0]} />
            </BarChart>
          </ResponsiveContainer>
        </div>
      </div>

      {/* Summary Table */}
      <div className="bg-white rounded-xl shadow-md border border-gray-200 p-6">
        <h3 className="text-lg font-bold text-gray-900 mb-6">Tng hp s liu</h3>
        <div className="overflow-x-auto">
          <table className="w-full text-left">
            <thead>
              <tr className="border-b border-gray-200">
                <th className="px-6 py-4 bg-gray-50 text-gray-700 font-semibold text-sm uppercase tracking-wider">Ch s</th>
                <th className="px-6 py-4 bg-gray-50 text-gray-700 font-semibold text-sm uppercase tracking-wider">Gi tr</th>
                <th className="px-6 py-4 bg-gray-50 text-gray-700 font-semibold text-sm uppercase tracking-wider">Ghi ch</th>
              </tr>
            </thead>
            <tbody>
              <tr className="border-b border-gray-100 hover:bg-gray-50">
                <td className="px-6 py-4 text-gray-700">Tng s vic lm</td>
                <td className="px-6 py-4 font-semibold text-gray-900">{stats?.total_jobs || 0}</td>
                <td className="px-6 py-4 text-gray-600">Bao gm tt c trng thi</td>
              </tr>
              <tr className="border-b border-gray-100 hover:bg-gray-50">
                <td className="px-6 py-4 text-gray-700">Vic lm ang tuyn</td>
                <td className="px-6 py-4 font-semibold text-green-600">{stats?.open_jobs || 0}</td>
                <td className="px-6 py-4 text-gray-600">ang nhn h s</td>
              </tr>
              <tr className="border-b border-gray-100 hover:bg-gray-50">
                <td className="px-6 py-4 text-gray-700">Tng h s ng tuyn</td>
                <td className="px-6 py-4 font-semibold text-gray-900">{stats?.total_applications || 0}</td>
                <td className="px-6 py-4 text-gray-600">Tt c h s  nhn</td>
              </tr>
              <tr className="border-b border-gray-100 hover:bg-gray-50">
                <td className="px-6 py-4 text-gray-700">im AI trung bnh</td>
                <td className="px-6 py-4 font-semibold text-blue-600">{stats?.avg_ai_score?.toFixed(1) || 'N/A'}</td>
                <td className="px-6 py-4 text-gray-600">im nh gi t ng</td>
              </tr>
              <tr className="border-b border-gray-100 hover:bg-gray-50">
                <td className="px-6 py-4 text-gray-700">Tng bui phng vn</td>
                <td className="px-6 py-4 font-semibold text-gray-900">{stats?.total_interviews || 0}</td>
                <td className="px-6 py-4 text-gray-600">{stats?.completed_interviews || 0}  hon thnh</td>
              </tr>
              <tr className="border-b border-gray-100 hover:bg-gray-50">
                <td className="px-6 py-4 text-gray-700">S offer  gi</td>
                <td className="px-6 py-4 font-semibold text-green-600">{stats?.total_offers || 0}</td>
                <td className="px-6 py-4 text-gray-600">ng vin c chn</td>
              </tr>
              <tr className="hover:bg-gray-50">
                <td className="px-6 py-4 text-gray-700">T l tuyn dng</td>
                <td className="px-6 py-4 font-semibold text-purple-600">{stats?.conversion_rate || 0}%</td>
                <td className="px-6 py-4 text-gray-600">Offer / Tng h s</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default Reports;
