import React, { useState, useEffect } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { getJob, getJobApplications, publishJob, closeJob, applyToJob } from '../services/api';
import Header from '../components/Header';
import Footer from '../components/Footer';
import {
  Briefcase, MapPin, Clock, Users, DollarSign, Calendar,
  Edit, CheckCircle, XCircle, ArrowLeft, FileText, Upload,
  Building, Award
} from 'lucide-react';

const JobDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const { user, isRecruiterOrAdmin } = useAuth();
  const [job, setJob] = useState(null);
  const [applications, setApplications] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showApplyForm, setShowApplyForm] = useState(false);
  const [applyData, setApplyData] = useState({
    cv_file: null,
    cover_letter: '',
    candidate_email: '',
    candidate_name: '',
  });
  const [applying, setApplying] = useState(false);
  const [applySuccess, setApplySuccess] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const jobRes = await getJob(id);
        setJob(jobRes.data);

        if (isRecruiterOrAdmin) {
          const appsRes = await getJobApplications(id);
          setApplications(Array.isArray(appsRes.data) ? appsRes.data : appsRes.data.results || []);
        }
      } catch (error) {
        console.error('Error fetching job:', error);
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, [id, isRecruiterOrAdmin]);

  const handlePublish = async () => {
    try {
      await publishJob(id);
      setJob({ ...job, status: 'OPEN' });
    } catch (error) {
      alert('Khng th ng vic lm');
    }
  };

  const handleClose = async () => {
    try {
      await closeJob(id);
      setJob({ ...job, status: 'CLOSED' });
    } catch (error) {
      alert('Khng th ng vic lm');
    }
  };

  const handleApply = async (e) => {
    e.preventDefault();
    
    // Validate
    if (!applyData.cv_file) {
      alert('Vui lng chn file CV');
      return;
    }
    
    // Validate file type
    const fileExt = applyData.cv_file.name.split('.').pop().toLowerCase();
    if (!['pdf', 'doc', 'docx'].includes(fileExt)) {
      alert('File CV phi l PDF hoc DOC/DOCX');
      return;
    }
    
    // Validate file size (max 10MB)
    if (applyData.cv_file.size > 10 * 1024 * 1024) {
      alert('File CV khng c vt qu 10MB');
      return;
    }
    
    setApplying(true);
    try {
      await applyToJob(id, applyData);
      setApplySuccess(true);
      setShowApplyForm(false);
      setApplyData({
        cv_file: null,
        cover_letter: '',
        candidate_email: '',
        candidate_name: '',
      });
      alert('Np h s thnh cng!');
      } catch (error) {
        console.error('Apply error:', error);
        console.error('Error response:', error.response?.data);
        
        // Hin th error message chi tit hn
        let errorMessage = 'Khng th np h s';
        if (error.response?.data?.detail) {
          errorMessage = error.response.data.detail;
        } else if (error.response?.data?.cv_file) {
          errorMessage = `CV: ${Array.isArray(error.response.data.cv_file) ? error.response.data.cv_file[0] : error.response.data.cv_file}`;
        } else if (error.response?.data?.job) {
          errorMessage = `Job: ${Array.isArray(error.response.data.job) ? error.response.data.job[0] : error.response.data.job}`;
        } else if (error.response?.data?.candidate_email) {
          errorMessage = `Email: ${Array.isArray(error.response.data.candidate_email) ? error.response.data.candidate_email[0] : error.response.data.candidate_email}`;
        } else if (error.response?.data) {
          // Ly error u tin
          const firstKey = Object.keys(error.response.data)[0];
          const firstError = error.response.data[firstKey];
          errorMessage = Array.isArray(firstError) ? firstError[0] : firstError;
        } else if (error.message) {
          errorMessage = error.message;
        }
        
        // Hin th error vi mu  r rng
        const errorDiv = document.createElement('div');
        errorDiv.className = 'fixed top-4 right-4 bg-red-600 text-white px-6 py-4 rounded-lg shadow-lg z-50 max-w-md';
        errorDiv.innerHTML = `
          <div class="flex items-center gap-3">
            <svg class="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"></path>
            </svg>
            <div>
              <p class="font-semibold">Li np h s</p>
              <p class="text-sm mt-1">${errorMessage}</p>
            </div>
          </div>
        `;
        document.body.appendChild(errorDiv);
        setTimeout(() => {
          errorDiv.remove();
        }, 5000);
    } finally {
      setApplying(false);
    }
  };

  const statusColors = {
    DRAFT: 'badge-gray',
    OPEN: 'badge-success',
    CLOSED: 'badge-danger',
  };

  const statusLabels = {
    PENDING: 'Ch x l',
    SCREENING: 'ang sng lc',
    INTERVIEW: 'Phng vn',
    OFFER: ' gi offer',
    REJECTED: 'T chi',
    ACCEPTED: ' nhn vic',
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  if (!job) {
    return (
      <div className="min-h-screen flex flex-col bg-gray-50">
        <Header />
        <main className="flex-1 flex items-center justify-center">
          <div className="text-center py-12">
            <h2 className="text-xl text-gray-900">Khng tm thy vic lm</h2>
            <Link to="/careers" className="mt-4 inline-block px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700">Quay li</Link>
          </div>
        </main>
        <Footer />
      </div>
    );
  }

  return (
    <div className="min-h-screen flex flex-col bg-gray-50">
      <Header />
      
      <main className="flex-1">
        <div className="max-w-5xl mx-auto px-4 py-8 space-y-6">
      {/* Header */}
      <div className="flex items-center gap-4 mb-6">
        <button
          onClick={() => navigate(-1)}
          className="p-2 rounded-lg hover:bg-gray-100 text-gray-600 hover:text-gray-900"
        >
          <ArrowLeft className="w-6 h-6" />
        </button>
        <Link to="/careers" className="text-sm text-gray-600 hover:text-blue-600">
           Quay li danh sch vic lm
        </Link>
      </div>

      {/* Job Info */}
      <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6">
        <div className="flex flex-col md:flex-row md:items-start md:justify-between gap-4 mb-6">
          <div>
            <div className="flex items-center gap-3 mb-2">
              <span className={`badge ${statusColors[job.status]}`}>
                {job.status === 'OPEN' ? 'ang tuyn' : job.status === 'DRAFT' ? 'Nhp' : ' ng'}
              </span>
              {job.employment_type && (
                <span className="badge badge-info">{job.employment_type}</span>
              )}
            </div>
            <h1 className="text-2xl font-bold text-gray-900 mb-2">{job.title}</h1>
            {job.department && (
              <p className="text-blue-600 flex items-center gap-2">
                <Building className="w-4 h-4" /> {job.department}
              </p>
            )}
          </div>

          {isRecruiterOrAdmin && (
            <div className="flex items-center gap-2">
              <Link to={`/jobs/${id}/edit`} className="btn-ghost flex items-center gap-2">
                <Edit className="w-4 h-4" /> Chnh sa
              </Link>
              {job.status === 'DRAFT' && (
                <button onClick={handlePublish} className="btn-success flex items-center gap-2">
                  <CheckCircle className="w-4 h-4" /> ng tuyn
                </button>
              )}
              {job.status === 'OPEN' && (
                <button onClick={handleClose} className="btn-danger flex items-center gap-2">
                  <XCircle className="w-4 h-4" /> ng tuyn
                </button>
              )}
            </div>
          )}
        </div>

        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
          <div className="flex items-center gap-3 text-gray-600">
            <MapPin className="w-5 h-5 text-gray-700" />
            <span>{job.location}</span>
          </div>
          <div className="flex items-center gap-3 text-gray-600">
            <Clock className="w-5 h-5 text-gray-700" />
            <span>Hn: {new Date(job.deadline).toLocaleDateString('vi-VN')}</span>
          </div>
          <div className="flex items-center gap-3 text-gray-600">
            <Users className="w-5 h-5 text-gray-700" />
            <span>{job.positions_count || 1} v tr</span>
          </div>
          {job.experience_years && (
            <div className="flex items-center gap-3 text-gray-600">
              <Award className="w-5 h-5 text-gray-700" />
              <span>{job.experience_years} nm KN</span>
            </div>
          )}
        </div>

        {(job.salary_min || job.salary_max || job.salary) && (
          <div className="p-4 bg-green-50 border border-green-200 rounded-lg mb-6">
            <div className="flex items-center gap-3 text-green-700">
              <DollarSign className="w-5 h-5" />
              <span className="font-semibold text-lg">
                {job.salary_min && job.salary_max
                  ? `${(Number(job.salary_min) / 1000000).toFixed(0)} - ${(Number(job.salary_max) / 1000000).toFixed(0)} triu`
                  : job.salary || 'Tha thun'}
              </span>
            </div>
          </div>
        )}

        <div className="space-y-6">
          <div>
            <h3 className="text-lg font-semibold text-gray-900 mb-3">M t cng vic</h3>
            <div className="text-gray-700 whitespace-pre-wrap leading-relaxed">{job.description}</div>
          </div>

          <div>
            <h3 className="text-lg font-semibold text-gray-900 mb-3">Yu cu</h3>
            <div className="text-gray-700 whitespace-pre-wrap leading-relaxed">{job.requirements}</div>
          </div>
        </div>

        {/* Apply Button */}
        {!isRecruiterOrAdmin && job.status === 'OPEN' && !applySuccess && (
          <div className="mt-6 pt-6 border-t border-gray-200">
            {!showApplyForm ? (
              <button
                onClick={() => setShowApplyForm(true)}
                className="w-full md:w-auto px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors flex items-center justify-center gap-2 font-semibold"
              >
                <FileText className="w-5 h-5" /> ng tuyn ngay
              </button>
            ) : (
              <form onSubmit={handleApply} className="space-y-4 bg-white p-6 rounded-lg border border-gray-200">
                <h3 className="text-xl font-bold text-gray-900 mb-6">Np h s ng tuyn</h3>

                {!user && (
                  <>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div>
                        <label className="label text-gray-900 font-semibold">H tn *</label>
                        <input
                          type="text"
                          value={applyData.candidate_name}
                          onChange={(e) => setApplyData({ ...applyData, candidate_name: e.target.value })}
                          className="input bg-white text-gray-900 border-gray-300 focus:border-blue-500 focus:ring-blue-500"
                          required
                        />
                      </div>
                      <div>
                        <label className="label text-gray-900 font-semibold">Email *</label>
                        <input
                          type="email"
                          value={applyData.candidate_email}
                          onChange={(e) => setApplyData({ ...applyData, candidate_email: e.target.value })}
                          className="input bg-white text-gray-900 border-gray-300 focus:border-blue-500 focus:ring-blue-500"
                          required
                        />
                      </div>
                    </div>
                  </>
                )}

                <div>
                  <label className="label text-gray-900 font-semibold">CV (PDF/DOCX) *</label>
                  <div className="relative">
                    <input
                      type="file"
                      accept=".pdf,.doc,.docx"
                      onChange={(e) => setApplyData({ ...applyData, cv_file: e.target.files[0] })}
                      className="input bg-white text-gray-900 border-gray-300 focus:border-blue-500 focus:ring-blue-500 file:mr-4 file:py-2 file:px-4 file:rounded-lg file:border-0 file:bg-blue-600 file:text-white file:cursor-pointer hover:file:bg-blue-700"
                      required
                    />
                  </div>
                </div>

                <div>
                  <label className="label text-gray-900 font-semibold">Th xin vic</label>
                  <textarea
                    value={applyData.cover_letter}
                    onChange={(e) => setApplyData({ ...applyData, cover_letter: e.target.value })}
                    className="input min-h-[100px] bg-white text-gray-900 border-gray-300 focus:border-blue-500 focus:ring-blue-500 placeholder:text-gray-500"
                    placeholder="Gii thiu v bn thn v l do bn ph hp vi v tr ny..."
                  />
                </div>

                <div className="flex items-center gap-4">
                  <button
                    type="button"
                    onClick={() => setShowApplyForm(false)}
                    className="btn-ghost"
                  >
                    Hy
                  </button>
                  <button
                    type="submit"
                    disabled={applying}
                    className="btn-primary flex items-center gap-2"
                  >
                    <Upload className="w-5 h-5" />
                    {applying ? 'ang gi...' : 'Gi h s'}
                  </button>
                </div>
              </form>
            )}
          </div>
        )}

        {applySuccess && (
          <div className="mt-6 p-4 bg-green-50 border border-green-200 rounded-lg text-green-800 flex items-center gap-3">
            <CheckCircle className="w-5 h-5 text-green-600" />
            <span className="font-medium">H s ca bn  c gi thnh cng! Chng ti s lin h sm.</span>
          </div>
        )}
      </div>

      {/* Applications (for recruiters) */}
      {isRecruiterOrAdmin && applications.length > 0 && (
        <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6 mt-6">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-gray-900">H s ng tuyn ({applications.length})</h3>
            <Link to={`/applications?job=${id}`} className="text-blue-600 hover:text-blue-700 text-sm font-medium">
              Xem tt c 
            </Link>
          </div>
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-700 uppercase tracking-wider">ng vin</th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-700 uppercase tracking-wider">Ngy np</th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-700 uppercase tracking-wider">im AI</th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-700 uppercase tracking-wider">Trng thi</th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-700 uppercase tracking-wider"></th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {applications.slice(0, 5).map((app) => (
                  <tr key={app.id} className="hover:bg-gray-50">
                    <td className="px-4 py-3 whitespace-nowrap font-medium text-gray-900">{app.candidate_name}</td>
                    <td className="px-4 py-3 whitespace-nowrap text-gray-600">{new Date(app.applied_at).toLocaleDateString('vi-VN')}</td>
                    <td className="px-4 py-3 whitespace-nowrap">
                      {app.ai_score ? (
                        <span className={`font-medium ${app.ai_score >= 70 ? 'text-green-600' : app.ai_score >= 50 ? 'text-yellow-600' : 'text-red-600'}`}>
                          {app.ai_score.toFixed(0)}
                        </span>
                      ) : '-'}
                    </td>
                    <td className="px-4 py-3 whitespace-nowrap">
                      <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${
                        app.status === 'ACCEPTED' ? 'bg-green-100 text-green-800' :
                        app.status === 'REJECTED' ? 'bg-red-100 text-red-800' :
                        app.status === 'INTERVIEW' ? 'bg-blue-100 text-blue-800' :
                        'bg-yellow-100 text-yellow-800'
                      }`}>
                        {statusLabels[app.status] || app.status}
                      </span>
                    </td>
                    <td className="px-4 py-3 whitespace-nowrap">
                      <Link
                        to={`/applications/${app.id}`}
                        className="text-blue-600 hover:text-blue-700 font-medium"
                      >
                        Chi tit
                      </Link>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}
        </div>
      </main>

      <Footer />
    </div>
  );
};

export default JobDetail;

