import React from 'react';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';
import { AuthProvider, useAuth } from './contexts/AuthContext';
import Layout from './components/Layout';
import CandidateLayout from './components/CandidateLayout';
import AdminLayout from './components/AdminLayout';
import RecruiterLayout from './components/RecruiterLayout';
import Login from './pages/Login';
import Register from './pages/Register';
import Dashboard from './pages/Dashboard';
import CandidateDashboard from './pages/CandidateDashboard';
import Jobs from './pages/Jobs';
import JobForm from './pages/JobForm';
import JobDetail from './pages/JobDetail';
import Applications from './pages/Applications';
import InterviewPanels from './pages/InterviewPanels';
import Interviews from './pages/Interviews';
import Results from './pages/Results';
import Processes from './pages/Processes';
import Reports from './pages/Reports';
import Careers from './pages/Careers';
import Home from './pages/Home';
import AdminDashboard from './pages/AdminDashboard';
import AdminUsers from './pages/AdminUsers';
import AdminSettings from './pages/AdminSettings';
import Profile from './pages/Profile';
import VerifyEmail from './pages/VerifyEmail';
import VerifyOTP from './pages/VerifyOTP';
import Settings from './pages/Settings';
import SavedJobs from './pages/SavedJobs';
import CandidateInterviews from './pages/CandidateInterviews';
import Notifications from './pages/Notifications';
import CompanyPage from './pages/CompanyPage';
import RecruitmentPipeline from './pages/RecruitmentPipeline';
import AdminCompanies from './pages/AdminCompanies';
import AdminJobs from './pages/AdminJobs';
import AdminInterviewers from './pages/AdminInterviewers';

// Ly role t environment variable
const APP_ROLE = process.env.REACT_APP_ROLE || 'ALL';

// Protected Route Component - S dng Layout ph hp vi role
const ProtectedRoute = ({ children, roles, useCandidateLayout = false }) => {
  const { user, loading } = useAuth();

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  if (!user) {
    return <Navigate to="/login" replace />;
  }

  if (roles && !roles.includes(user.role)) {
      // Redirect v dashboard ph hp vi role (3 roles chnh)
      if (user.role === 'CANDIDATE') {
        return <Navigate to="/careers" replace />;
    } else if (user.role === 'ADMIN') {
      return <Navigate to="/admin/dashboard" replace />;
    } else if (user.role === 'RECRUITER') {
      return <Navigate to="/dashboard" replace />;
    } else {
      // Role khng hp l, redirect v home
      return <Navigate to="/" replace />;
    }
  }

  // CANDIDATE s dng CandidateLayout (ch header/footer)
  // ADMIN s dng AdminLayout (header ring, khng footer)
  // RECRUITER s dng RecruiterLayout (header ring, c footer)
  if (useCandidateLayout || user.role === 'CANDIDATE') {
    return <CandidateLayout>{children}</CandidateLayout>;
  }

  if (user.role === 'ADMIN') {
    return <AdminLayout>{children}</AdminLayout>;
  }

  if (user.role === 'RECRUITER') {
    return <RecruiterLayout>{children}</RecruiterLayout>;
  }

  // Fallback
  return <Layout>{children}</Layout>;
};

// Public Route (redirect if logged in)
const PublicRoute = ({ children }) => {
  const { user, loading } = useAuth();

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  if (user) {
    // Redirect v dashboard ph hp vi role
    if (user.role === 'ADMIN') {
      return <Navigate to="/admin/dashboard" replace />;
    } else if (user.role === 'CANDIDATE') {
      return <Navigate to="/careers" replace />;
    } else if (user.role === 'RECRUITER') {
      return <Navigate to="/dashboard" replace />;
    } else {
      // ADMIN hoc role khc
      return <Navigate to="/admin/dashboard" replace />;
    }
  }

  return children;
};

function AppRoutes() {
  // Hin th TT C routes - Phn quyn da trn user  ng nhp
  // Cho php chy 3 roles trn cng 1 port, test bng cch ng nhp vi ti khon khc nhau

  // Redirect root path based on APP_ROLE
  const RootRedirect = () => {
    if (APP_ROLE === 'ADMIN') {
      return <Navigate to="/login" replace />;
    }
    return <Home />;
  };

  return (
    <Routes>
      {/* ============================================
          PUBLIC ROUTES (Khng cn ng nhp)
          ============================================ */}
      <Route path="/" element={<RootRedirect />} />
      <Route path="/login" element={<PublicRoute><Login /></PublicRoute>} />
      <Route path="/register" element={<PublicRoute><Register /></PublicRoute>} />
      <Route path="/verify-email" element={<VerifyEmail />} />
      <Route path="/verify-otp" element={<VerifyOTP />} />
      <Route path="/careers" element={<Careers />} />
      <Route path="/jobs/:id" element={<JobDetail />} />
      <Route path="/companies/:id" element={<CompanyPage />} />

      {/* ============================================
          CANDIDATE ROUTES (ng vin)
          Phn quyn: ProtectedRoute s check role ca user
          ============================================ */}
      {/* Candidate dashboard removed - redirect to careers */}
      <Route 
        path="/candidate/dashboard" 
        element={<Navigate to="/careers" replace />} 
      />
      <Route 
        path="/profile" 
        element={<ProtectedRoute useCandidateLayout><Profile /></ProtectedRoute>} 
      />
      <Route 
        path="/saved-jobs" 
        element={<ProtectedRoute useCandidateLayout><SavedJobs /></ProtectedRoute>} 
      />
      <Route 
        path="/candidate/interviews" 
        element={<ProtectedRoute useCandidateLayout><CandidateInterviews /></ProtectedRoute>} 
      />
      <Route 
        path="/notifications" 
        element={<ProtectedRoute useCandidateLayout><Notifications /></ProtectedRoute>} 
      />

      {/* ============================================
          RECRUITER ROUTES (Nh tuyn dng)
          Phn quyn: ProtectedRoute s check role ca user
          ============================================ */}
      <Route 
        path="/dashboard" 
        element={<ProtectedRoute roles={['RECRUITER']}><Dashboard /></ProtectedRoute>} 
      />
      
      {/* Jobs Management */}
      <Route 
        path="/jobs" 
        element={<ProtectedRoute roles={['RECRUITER']}><Jobs /></ProtectedRoute>} 
      />
      <Route 
        path="/jobs/new" 
        element={<ProtectedRoute roles={['RECRUITER']}><JobForm /></ProtectedRoute>} 
      />
      <Route 
        path="/jobs/:id/edit" 
        element={<ProtectedRoute roles={['RECRUITER']}><JobForm /></ProtectedRoute>} 
      />

      {/* Applications */}
      <Route 
        path="/applications" 
        element={<ProtectedRoute roles={['RECRUITER']}><Applications /></ProtectedRoute>} 
      />

      {/* Recruitment Pipeline */}
      <Route 
        path="/pipeline" 
        element={<ProtectedRoute roles={['RECRUITER']}><RecruitmentPipeline /></ProtectedRoute>} 
      />

      {/* Interviews */}
      <Route 
        path="/interviews" 
        element={<ProtectedRoute roles={['RECRUITER']}><Interviews /></ProtectedRoute>} 
      />

      {/* Interview Panels */}
      <Route 
        path="/panels" 
        element={<ProtectedRoute roles={['RECRUITER']}><InterviewPanels /></ProtectedRoute>} 
      />

      {/* Results */}
      <Route 
        path="/results" 
        element={<ProtectedRoute roles={['RECRUITER']}><Results /></ProtectedRoute>} 
      />

      {/* Processes */}
      <Route 
        path="/processes" 
        element={<ProtectedRoute roles={['RECRUITER']}><Processes /></ProtectedRoute>} 
      />

      {/* Reports */}
      <Route 
        path="/reports" 
        element={<ProtectedRoute roles={['RECRUITER']}><Reports /></ProtectedRoute>} 
      />

      {/* Settings */}
      <Route 
        path="/settings" 
        element={<ProtectedRoute roles={['RECRUITER']}><Settings /></ProtectedRoute>} 
      />

      {/* ============================================
          ADMIN ROUTES (Qun tr h thng)
          Phn quyn: ProtectedRoute s check role ca user
          ============================================ */}
      <Route 
        path="/admin/dashboard" 
        element={<ProtectedRoute roles={['ADMIN']}><AdminDashboard /></ProtectedRoute>} 
      />
      <Route 
        path="/admin/users" 
        element={<ProtectedRoute roles={['ADMIN']}><AdminUsers /></ProtectedRoute>} 
      />
      <Route 
        path="/admin/settings" 
        element={<ProtectedRoute roles={['ADMIN']}><AdminSettings /></ProtectedRoute>} 
      />
      <Route 
        path="/admin/companies" 
        element={<ProtectedRoute roles={['ADMIN']}><AdminCompanies /></ProtectedRoute>} 
      />
      <Route 
        path="/admin/jobs" 
        element={<ProtectedRoute roles={['ADMIN']}><AdminJobs /></ProtectedRoute>} 
      />

      {/* ============================================
          DEFAULT REDIRECT
          ============================================ */}
      <Route path="*" element={<Navigate to="/" replace />} />
    </Routes>
  );
}

function App() {
  return (
    <BrowserRouter>
      <AuthProvider>
        <AppRoutes />
      </AuthProvider>
    </BrowserRouter>
  );
}

export default App;
