import React, { createContext, useContext, useState, useEffect } from 'react';
import { login as apiLogin, register as apiRegister, getMe } from '../services/api';

const AuthContext = createContext(null);

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  
  // Option  enable/disable sync gia cc tab
  // Set false  mi tab hot ng c lp (ph hp cho demo)
  const SYNC_BETWEEN_TABS = false; // true = sync, false = independent

  // Load user t token khi component mount
  const loadUser = async () => {
    const token = localStorage.getItem('access_token');
    if (token) {
      try {
        const res = await getMe();
        setUser(res.data);
      } catch (error) {
        // Token invalid, clear storage
        localStorage.removeItem('access_token');
        localStorage.removeItem('refresh_token');
        setUser(null);
      }
    } else {
      setUser(null);
    }
    setLoading(false);
  };

  useEffect(() => {
    loadUser();

    if (SYNC_BETWEEN_TABS) {
      // Listen for storage changes (khi login/logout  tab khc)
      // Ch enable nu mun sync gia cc tab
      const handleStorageChange = (e) => {
        if (e.key === 'access_token' || e.key === 'refresh_token') {
          loadUser();
        }
      };

      // Listen for custom storage event (same-tab updates)
      const handleCustomStorage = (e) => {
        if (e.detail?.key === 'access_token' || e.detail?.key === 'refresh_token') {
          loadUser();
        }
      };

      window.addEventListener('storage', handleStorageChange);
      window.addEventListener('auth-storage-change', handleCustomStorage);

      return () => {
        window.removeEventListener('storage', handleStorageChange);
        window.removeEventListener('auth-storage-change', handleCustomStorage);
      };
    }
  }, []);

  const login = async (email, password) => {
    try {
      const response = await apiLogin(email, password);
      
      // Kim tra response c y  khng
      if (!response.data || !response.data.access) {
        throw new Error('Invalid response from server');
      }
      
      localStorage.setItem('access_token', response.data.access);
      localStorage.setItem('refresh_token', response.data.refresh);
      setUser(response.data.user);
      
      // Notify other tabs about auth change (ch nu sync enabled)
      if (SYNC_BETWEEN_TABS) {
        window.dispatchEvent(new CustomEvent('auth-storage-change', {
          detail: { key: 'access_token' }
        }));
      }
      
      return response.data;
    } catch (error) {
      // Re-throw  component c th x l
      throw error;
    }
  };

  const register = async (data) => {
    const response = await apiRegister(data);
    // KHNG t ng ng nhp nu cn verify email
    // User phi verify email trc khi c th login
    if (response.data && response.data.requires_verification) {
      // Khng t ng login, user cn verify email trc
      return response.data;
    }
    
    // Nu khng cn verify (trng hp c bit), mi t ng login
    if (response.data && response.data.user && !response.data.requires_verification) {
      try {
        // Login  ly token
        const loginResponse = await apiLogin(data.email, data.password);
        if (loginResponse.data) {
          localStorage.setItem('access_token', loginResponse.data.access);
          localStorage.setItem('refresh_token', loginResponse.data.refresh);
          setUser(loginResponse.data.user || response.data.user);
          
          // Notify other tabs about auth change (ch nu sync enabled)
          if (SYNC_BETWEEN_TABS) {
            window.dispatchEvent(new CustomEvent('auth-storage-change', {
              detail: { key: 'access_token' }
            }));
          }
        }
      } catch (err) {
        console.error('Auto-login after registration failed:', err);
        // Vn return success, user c th login th cng
      }
    }
    return response.data;
  };

  const logout = () => {
    localStorage.removeItem('access_token');
    localStorage.removeItem('refresh_token');
    setUser(null);
    
    // Notify other tabs about logout (ch nu sync enabled)
    if (SYNC_BETWEEN_TABS) {
      window.dispatchEvent(new CustomEvent('auth-storage-change', {
        detail: { key: 'access_token' }
      }));
    }
  };

  // 3 Roles chnh:
  // 1. ADMIN - Qun l h thng
  // 2. CANDIDATE - ng vin tm vic
  // 3. RECRUITER - Nh tuyn dng (doanh nghip/c nhn)
  
  const isAdmin = user?.role === 'ADMIN';
  const isCandidate = user?.role === 'CANDIDATE';
  const isRecruiter = user?.role === 'RECRUITER';
  // ADMIN cng c th lm recruiter (qun l vic lm)
  const isRecruiterOrAdmin = isRecruiter || isAdmin;

  return (
    <AuthContext.Provider value={{ 
      user, 
      setUser, 
      loading, 
      login, 
      register, 
      logout, 
      isAdmin,
      isCandidate,
      isRecruiter,
      isRecruiterOrAdmin, // Helper  check quyn recruiter (bao gm c ADMIN)
      // Backward compatibility
      isRecruiter: isRecruiterOrAdmin,
      isInterviewer: isRecruiterOrAdmin, // INTERVIEWER c x l nh RECRUITER
    }}>
      {children}
    </AuthContext.Provider>
  );
};

