import React, { useState, useEffect } from 'react';
import { useAuth } from '../../contexts/AuthContext';
import { Building, Search, Plus, CheckCircle, AlertCircle, Camera } from 'lucide-react';
import LocationSelector from '../LocationSelector';

const CompanyInfo = () => {
  const { user } = useAuth();
  const [searchMode, setSearchMode] = useState('create'); // search, create
  const [companyType, setCompanyType] = useState('enterprise'); // enterprise, household
  const [formData, setFormData] = useState({
    tax_id: '',
    company_name: '',
    website: '',
    field_of_activity: '',
    scale: '',
    address: '',
    email: '',
    phone: '',
    description: '',
    province: '',
    district: ''
  });
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState({ type: '', text: '' });

  useEffect(() => {
    if (user) {
      setFormData({
        tax_id: user.tax_id || '',
        company_name: user.company_name || '',
        website: user.website || '',
        field_of_activity: user.field_of_activity || '',
        scale: user.scale || '',
        address: user.address || '',
        email: user.company_email || user.email || '',
        phone: user.phone || '',
        description: user.company_description || '',
        province: user.work_location_province || '',
        district: user.work_location_district || ''
      });
    }
  }, [user]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
    setMessage({ type: '', text: '' });
  };

  const handleLocationChange = (location) => {
    setFormData({
      ...formData,
      province: location.province,
      district: location.district
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage({ type: '', text: '' });

    try {
      // TODO: Implement API call to update company info
      // await updateCompanyInfo(formData);
      
      setMessage({ type: 'success', text: 'Cp nht thng tin cng ty thnh cng!' });
    } catch (error) {
      setMessage({
        type: 'error',
        text: error.response?.data?.error || 'Cp nht tht bi. Vui lng th li.'
      });
    } finally {
      setLoading(false);
    }
  };

  const fieldsOfActivity = [
    'Cng ngh thng tin',
    'Ti chnh - Ngn hng',
    'Bt ng sn',
    'Gio dc - o to',
    'Y t - Chm sc sc khe',
    'Thng mi in t',
    'Sn xut',
    'Dch v',
    'Marketing - Qung co',
    'Logistics - Vn ti'
  ];

  const companyScales = [
    'Di 10 nhn vin',
    '10 - 50 nhn vin',
    '50 - 200 nhn vin',
    '200 - 500 nhn vin',
    'Trn 500 nhn vin'
  ];

  return (
    <div>
      <h2 className="text-xl font-bold text-gray-900 mb-6">Thng tin cng ty</h2>

      {/* Search or Create */}
      <div className="grid grid-cols-2 gap-4 mb-6">
        <button
          type="button"
          onClick={() => setSearchMode('search')}
          className={`p-4 rounded-lg border-2 transition-colors ${
            searchMode === 'search'
              ? 'border-green-500 bg-green-50'
              : 'border-gray-200 hover:border-gray-300'
          }`}
        >
          <div className="flex items-center gap-3">
            <Search className={`w-6 h-6 ${searchMode === 'search' ? 'text-green-600' : 'text-gray-600'}`} />
            <div className="text-left">
              <p className={`font-medium ${searchMode === 'search' ? 'text-green-700' : 'text-gray-700'}`}>
                Tm kim thng tin cng ty
              </p>
              <p className="text-xs text-gray-700">Dnh cho Doanh nghip  c trn TopCV</p>
            </div>
          </div>
        </button>
        <button
          type="button"
          onClick={() => setSearchMode('create')}
          className={`p-4 rounded-lg border-2 transition-colors relative ${
            searchMode === 'create'
              ? 'border-green-500 bg-green-50'
              : 'border-gray-200 hover:border-gray-300'
          }`}
        >
          <div className="flex items-center gap-3">
            <Plus className={`w-6 h-6 ${searchMode === 'create' ? 'text-green-600' : 'text-gray-600'}`} />
            <div className="text-left">
              <p className={`font-medium ${searchMode === 'create' ? 'text-green-700' : 'text-gray-700'}`}>
                To cng ty mi
              </p>
              <p className="text-xs text-gray-700">Dnh cho Doanh nghip ln u s dng TopCV</p>
            </div>
          </div>
          {searchMode === 'create' && (
            <CheckCircle className="absolute top-2 right-2 w-5 h-5 text-green-600" />
          )}
        </button>
      </div>

      {/* Company Logo */}
      <div className="mb-6">
        <label className="block text-sm font-medium text-gray-700 mb-2">Logo cng ty</label>
        <div className="flex items-center gap-4">
          <div className="w-24 h-24 rounded-lg bg-gray-200 flex items-center justify-center overflow-hidden border-2 border-dashed border-gray-300">
            <Building className="w-12 h-12 text-gray-600" />
          </div>
          <button
            type="button"
            className="px-4 py-2 bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-lg text-sm transition-colors flex items-center gap-2"
          >
            <Camera className="w-4 h-4" />
            i logo
          </button>
        </div>
      </div>

      {/* Company Type */}
      <div className="mb-6">
        <div className="flex gap-2 bg-gray-100 rounded-lg p-1 inline-flex">
          <button
            type="button"
            onClick={() => setCompanyType('enterprise')}
            className={`px-6 py-2 rounded-lg text-sm font-medium transition-colors ${
              companyType === 'enterprise'
                ? 'bg-green-500 text-white'
                : 'text-gray-700 hover:bg-gray-200'
            }`}
          >
            Doanh nghip
          </button>
          <button
            type="button"
            onClick={() => setCompanyType('household')}
            className={`px-6 py-2 rounded-lg text-sm font-medium transition-colors ${
              companyType === 'household'
                ? 'bg-green-500 text-white'
                : 'text-gray-700 hover:bg-gray-200'
            }`}
          >
            H kinh doanh
          </button>
        </div>
      </div>

      {/* Warning Banner */}
      <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4 mb-6">
        <div className="flex items-start gap-2">
          <span className="text-yellow-600"></span>
          <p className="text-sm text-yellow-800">
             ti khon c xc thc nhanh chng, vui lng nhp <strong>M s thu</strong> v <strong>Tn cng ty</strong> trng khp vi d liu doanh nghip theo Trang thng tin in t ca Cc Thu. Bn c th tra cu thng tin doanh nghip{' '}
            <a href="#" className="text-blue-600 hover:underline">Ti y</a>.
          </p>
        </div>
      </div>

      {message.text && (
        <div className={`mb-4 p-4 rounded-lg flex items-center gap-3 ${
          message.type === 'success'
            ? 'bg-green-50 text-green-700 border border-green-200'
            : 'bg-red-50 text-red-700 border border-red-200'
        }`}>
          {message.type === 'success' ? (
            <CheckCircle className="w-5 h-5" />
          ) : (
            <AlertCircle className="w-5 h-5" />
          )}
          <span>{message.text}</span>
        </div>
      )}

      <form onSubmit={handleSubmit} className="space-y-6">
        <div className="grid grid-cols-2 gap-6">
          {/* Left Column */}
          <div className="space-y-4">
            {/* Tax ID */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                M s thu <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                name="tax_id"
                value={formData.tax_id}
                onChange={handleChange}
                placeholder="Nhp m s thu"
                className="input w-full"
                required
              />
            </div>

            {/* Website */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Website</label>
              <div className="flex items-center">
                <span className="px-3 py-2 bg-gray-100 border border-r-0 border-gray-300 rounded-l-lg text-gray-700 text-sm">
                  https://
                </span>
                <input
                  type="text"
                  name="website"
                  value={formData.website}
                  onChange={handleChange}
                  placeholder="example.com"
                  className="input flex-1 rounded-l-none"
                />
              </div>
            </div>

            {/* Scale */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Quy m <span className="text-red-500">*</span>
              </label>
              <select
                name="scale"
                value={formData.scale}
                onChange={handleChange}
                className="input w-full"
                required
              >
                <option value="">Chn quy m cng ty</option>
                {companyScales.map((scale) => (
                  <option key={scale} value={scale}>{scale}</option>
                ))}
              </select>
            </div>

            {/* Email */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Email <span className="text-red-500">*</span>
              </label>
              <input
                type="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                placeholder="Nhp email cng ty"
                className="input w-full"
                required
              />
            </div>

            {/* Description */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                M t cng ty
              </label>
              <textarea
                name="description"
                value={formData.description}
                onChange={handleChange}
                rows={6}
                placeholder="Nhp m t v cng ty..."
                className="input w-full"
              />
              <p className="text-xs text-gray-700 mt-1">
                TopCV khuyn khch  di ti thiu 100 t
              </p>
            </div>
          </div>

          {/* Right Column */}
          <div className="space-y-4">
            {/* Company Name */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Tn cng ty <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                name="company_name"
                value={formData.company_name}
                onChange={handleChange}
                placeholder="Nhp tn cng ty"
                className="input w-full"
                required
              />
            </div>

            {/* Field of Activity */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Lnh vc hot ng <span className="text-red-500">*</span>
              </label>
              <select
                name="field_of_activity"
                value={formData.field_of_activity}
                onChange={handleChange}
                className="input w-full"
                required
              >
                <option value="">Chn lnh vc hot ng</option>
                {fieldsOfActivity.map((field) => (
                  <option key={field} value={field}>{field}</option>
                ))}
              </select>
            </div>

            {/* Address */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                a ch <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                name="address"
                value={formData.address}
                onChange={handleChange}
                placeholder="Nhp a ch cng ty"
                className="input w-full"
                required
              />
            </div>

            {/* Location */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                a im lm vic
              </label>
              <LocationSelector
                selectedProvince={formData.province}
                selectedDistrict={formData.district}
                onChange={handleLocationChange}
                placeholder="Chn tnh/thnh ph"
              />
            </div>

            {/* Phone */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                S in thoi <span className="text-red-500">*</span>
              </label>
              <input
                type="tel"
                name="phone"
                value={formData.phone}
                onChange={handleChange}
                placeholder="Nhp s in thoi"
                className="input w-full"
                required
              />
            </div>
          </div>
        </div>

        {/* Action Buttons */}
        <div className="flex items-center justify-end gap-3 pt-4 border-t border-gray-200">
          <button
            type="button"
            onClick={() => window.location.reload()}
            className="px-6 py-2 bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-lg transition-colors"
          >
            Hy
          </button>
          <button
            type="submit"
            disabled={loading}
            className="px-6 py-2 bg-green-500 hover:bg-green-600 text-white rounded-lg transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {loading ? 'ang x l...' : 'Lu'}
          </button>
        </div>
      </form>
    </div>
  );
};

export default CompanyInfo;

